.btn {
  display: inline-block;
  position: relative;
}

.btn::before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: 0.3125rem;
  left: 0.3125rem;
}

.btn span {
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  background-color: #408F95;
  border: 0.0625rem solid #408F95;
  padding: 0.9375rem 1.9375rem 0.9375rem 2.125rem;
  position: relative;
  transition: 0.3s ease;
  overflow: hidden;
  z-index: 1;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  color: #fff;
  word-break: break-word;
}

.btn span::before {
  content: "";
  display: inline-flex;
  margin-top: -0.25rem;
  width: 3.8125rem;
  height: 0.5rem;
  background: url(../images/common/arrow.svg) no-repeat center/contain;
  transition: 0.3s ease;
}

.btn span::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .btn span:hover {
    color: #408F95;
  }
}

@media screen and (min-width: 768px) {
  .btn span:hover::before {
    background: url(../images/common/arrow_green.svg) no-repeat center/contain;
  }
}

@media screen and (min-width: 768px) {
  .btn span:hover::after {
    transform: scale(1, 1);
  }
}

/*===== 404ページ =====*/
.btn.btn--white::before {
  border: 0.0625rem solid #fff;
}

.btn.btn--white span {
  padding: 0.875rem 2.0625rem 0.875rem 2.5625rem;
  background-color: #fff;
  color: #408F95;
  border: 0.0625rem solid #fff;
}

.btn.btn--white span::before {
