@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////
$main: 'Noto Sans JP',
sans-serif;
$gotu: 'Gotu',
sans-serif;
$lato: 'Lato',
sans-serif;

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #111;
$white: #fff;
$green:#408F95;
// $green:#0095c7;
$brown:#796A64;
$gray:#F2F2F2;
$blue:#DDF0F1;
$navy:#0D2936;

body {
  font-family: $main;
  color: $navy;
}

body.is-active {
  overflow: hidden;
}

body.blog {
padding-top: 0;
}
body.blog::before {
display: none;
}
